  margin-bottom: 1.25rem;
  opacity: 0;
}

/* Buy now */
._index-module__card__button__3uqhS {
  display: block;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 1.125rem 2rem;
  background-color: var(--first-color);
  color: var(--white-color);
  border-radius: 0.5rem;
  font-weight: 600;
  transition: 0.2s;
  opacity: 0;
}

._index-module__card__button__3uqhS:hover {
  box-shadow: 0 18px 40px -12px rgba(242, 162, 12, 0.35);
}

._index-module__card__img__VAqPZ,
._index-module__card__data__1i8b5,
._index-module__card__title__1TFZX,
._index-module__card__preci__158hu,
._index-module__card__description__3VrFM {
  transition: 0.5s;
}

/* Hover card effect */
._index-module__card__2uBKN:hover ._index-module__card__img__VAqPZ {
  transform: translate(-1.5rem, -9.5rem) rotate(-20deg);
}

._index-module__card__2uBKN:hover ._index-module__card__data__1i8b5 {
  transform: translateY(4.8rem);
}

._index-module__card__2uBKN:hover ._index-module__card__title__1TFZX {
  transform: translateX(-2.3rem);
  margin-bottom: 0;
}

._index-module__card__2uBKN:hover ._index-module__card__preci__158hu {
  transform: translateX(-6.8rem);
}

._index-module__card__2uBKN:hover ._index-module__card__description__3VrFM,
._index-module__card__2uBKN:hover ._index-module__card__button__3uqhS {
